.distantWorkBlock {
    height: 30vh;
    background-color: #10101a;;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.distantWorkContainer {
    flex-direction: column;
}

.span {
    background-color: #B94629;
}

.button {
    background-color: #f1f1f1;
    color: black;
    font-size: 20px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    text-align: center;
}

.button:hover {
    background-color: black;
    color: white;
}

.button:active {
    background-color: red;
}